<template>
  <div class="amoutSearchResult">
    <v-container
      fluid
      grid-list-xl>
      <v-layout>
        <v-flex
          md6
          tag="h3"
          class="mt-0">
          {{ $ln('額度查詢搜尋結果') }}
        </v-flex>
        <v-flex
          md6
          class="text-md-right">
          <v-btn
            small
            color="amber darken-3"
            @click="toBack()">
            <v-icon class="mr-2">
              mdi-arrow-left-thick
            </v-icon>
            {{ $ln('返回') }}
          </v-btn>
        </v-flex>
      </v-layout>

      <div
        v-if="loadingStatus"
        class="text-xs-center">
        <MaterialLoadingPage :show-text="'搜尋中'"/>
      </div>
      <div v-else>
        <div>
          {{ $ln('查詢類型') }}：{{ lv===1?$ln('代理'):$ln('會員') }} <br>
          {{ $ln('帳務日期') }}：{{ time_start }} ~ {{ time_end }}
        </div>

        <div class="pt-1" >
          <v-data-table
            :headers="tableData1.headers"
            :items="tableData1.contents"
            :no-data-text="$ln(tableData1.showNoData)"
            hide-actions
            class="elevation-1">
            <template
              slot="headerCell"
              slot-scope="{ header }">
              <span class="column">
                {{ header.text ? $ln(header.text) : header.text }}
              </span>
            </template>
            <template
              slot="items"
              slot-scope="{ item }">
              <td class="text-xs-center">{{ item.listAcc }}({{ item.nickname }})</td>
              <td class="text-xs-right">{{ item.b_count }}</td>
              <td :class="['text-xs-right',
                          {'red--text': parseFloat(item.all_gold) < 0,
                           'green--text': parseFloat(item.all_gold) > 0}]">
                {{ item.all_gold }}
              </td>
              <td class="text-xs-center">
                <router-link :to="{ path: '/manage/report/amout-search/Details', query: { time_start: time_start, time_end:time_end, lv: lv, account: account=item.listAcc } }">
                  {{ $ln('明細') }}
                </router-link>
              </td>
            </template>
            <template slot="footer">
              <td
                class="text-xs-right"
                colspan="1">
                <strong>{{ $ln('合計') }}</strong>
              </td>
              <td
                class="text-xs-right"
                colspan="1">
                <strong>{{ tableData1.count }}</strong>
              </td>
              <td
                :class="['text-xs-right',
                        {'red--text': parseFloat(tableData1.amount) < 0,
                         'green--text': parseFloat(tableData1.amount) > 0}]"
                colspan="1">
                <strong>{{ tableData1.amount }}</strong>
              </td>
              <td
                class="text-xs-center"
                colspan="1">
                <strong/>
              </td>
            </template>
          </v-data-table>
          <!-- <v-btn
            small
            color="success"
            @click="toBack()">返回</v-btn> -->
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import LoadingPage from '@/components/material/LoadingPage'
export default {
  name: 'AmoutSearchResult',
  components: {
    LoadingPage
  },
  data () {
    return {
      i18n: {},
      // 載入資料列表1
      tableData1: {
        showNoData: '目前無資料',
        headers: [
          { text: '代理帳號', align: 'center', sortable: false },
          { text: '額度筆數', align: 'center', sortable: false },
          { text: '額度金額', align: 'center', sortable: false },
          { text: '明細', align: 'center', sortable: false }
        ],
        contents: [],
        count: '',
        amount: ''
      },
      time_start: this.$route.query.time_start,
      time_end: this.$route.query.time_end,
      lv: this.$route.query.lv,
      // 頁面載入狀態
      loadingStatus: false
    }
  },
  computed: {
    ...mapGetters({
      getQuotaSearchState: 'report/getQuotaSearchState'
    })
  },
  watch: {
    getQuotaSearchState (payload) {
      this.tableData1.contents = payload.result.list
      this.tableData1.count = payload.result.count
      this.tableData1.amount = payload.result.amount
    },
    'time_start' (value) {
      console.log('time_start', value)
    },
    'time_end' (value) {
      console.log('time_end', value)
    }
  },
  mounted () {
    this.loadQuotaSearch()
    if (this.$route.query.lv === 0) {
      this.tableData1.headers[0].text = '會員帳號'
    }
  },
  methods: {
    ...mapActions({
      getQuotaSearch: 'api/getQuotaSearch'
    }),
    // 載入帳務紀錄搜尋結果資料
    loadQuotaSearch (page, rowsPerPage) {
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        time_start: this.$route.query.time_start,
        time_end: this.$route.query.time_end,
        lv: this.$route.query.lv
        // click_acc: this.$route.query.click_acc
      }
      this.getQuotaSearch(params)
        .then(res => {
          if (Object.prototype.toString.call(res) === '[object Error]' &&
              res.hasOwnProperty('response') &&
              res.response.status !== 200) {
            this.toBack()
            console.error(res.response)
          } else if (res.status === 200) {
            console.log('回傳成功')
          }
          // 載入狀態(結束)
          this.loadingStatus = false
        })
        .catch(err => {
          console.log(err)
          // 載入狀態(結束)
          this.loadingStatus = false
          this.toBack()
        })
    },
    toBack () {
      this.$router.push({
        path: '/manage/report/amout-search'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
table.v-table tfoot tr td {
  padding: 0px 5px;
}
</style>
